import Vue from 'vue';
import VueI18n from 'vue-i18n';
import Cookies from 'js-cookie'
import Router from 'vue-router';
import zhCnLocale from 'iview/src/locale/lang/zh-CN';
import enUsLocale from 'iview/src/locale/lang/en-US';
import zhTwLocale from 'iview/src/locale/lang/zh-TW';
    /**
     * i18nmain.jsVue.use({i18n: (key, value) => i18n.t(key, value)})
     * @param {*} zhCnLocale  { home: '',  components: ''}
     * @param {*} zhTwLocale  
     * @param {*} enUsLocale 
     */
    export const setVueI18n=(customZhCn,customZhTw,customEnUs)=>{
    Vue.use(VueI18n);
    // 
    const navLang = navigator.language
    const localLang = (navLang === 'zh-CN' || navLang === 'en-US') ? navLang : false
    let lang = localLang || localRead('local') || 'zh-CN'//localLanglocalRead('local')
    Vue.config.lang = lang
    const messages = {
        'zh-CN': Object.assign(zhCnLocale, customZhCn),
        'zh-TW': Object.assign(zhTwLocale, customZhTw),
        'en-US': Object.assign(enUsLocale, customEnUs)
      }
      const i18n = new VueI18n({
        locale: lang,
        messages
      })
      return i18n;

};
    /**
     * keylocalStorage
     * @param {*} key 
     */
    export const localRead=(key)=>{
       return  localStorage.getItem(key) || '';//locallocalStorage
       
    }





 
    export const setToken = (token) => {
      Cookies.set(TOKEN_KEY, token, {expires:1})// config,Cookies.set(TOKEN_KEY, token, {expires: config.cookieExpires || 1})
    }
    
    /**
     * 
     * @param {*} TOKEN_KEY key
     */
    export const getToken = (TOKEN_KEY) => {
      const token = Cookies.get(TOKEN_KEY)
      if (token) return token
      else return false
    }


    /**
     * 
     * @param {*} LOGIN_PAGE_NAME 
     * @param {*} routes  {path: '/login',name: 'login',meta:{title: 'login',hideInMenu: true},component: ab}
     * @param {*} TOKEN_KEY key
     * @param {*} homeName 
     */
  export const setRouter=(LOGIN_PAGE_NAME,routes,TOKEN_KEY,homeName) =>{
      Vue.use(Router);
  const router = new Router({
  routes,
  mode: 'history'
  });
  router.beforeEach((to, from, next) => {
    iView.LoadingBar.start()
    const token = getToken(TOKEN_KEY)
    if (!token && to.name !== LOGIN_PAGE_NAME) {   
      // 
      next({
        name: LOGIN_PAGE_NAME // 
      })
    } else if (!token && to.name === LOGIN_PAGE_NAME) {
      // 
      next() // 
    } else if (token && to.name === LOGIN_PAGE_NAME) {
      // 
      next({
        name: homeName // homeName
      })
    } else {//,
     
    }
  });
  router.afterEach(to => {
    iView.LoadingBar.finish()
    window.scrollTo(0, 0)
  });
  return router;
    }



