import {canteenAdd, getCanteenObj,deleteCanteen,getOneCanteen} from "@/api/api";

let canteen={
    state:{canteenId:'',canteenName:'',canteenLabel:'',canteenDes:'',canteenImage:''},
    mutations:{
        setcanteen(state,canteen){
            state.canteenId=canteen.canteenId;
            state.canteenName=canteen.canteenName;
            state.canteenLabel=canteen.canteenLabel;
            state.canteenDes=canteen.canteenDes;
            state.canteenImage=canteen.canteenImage;
        }
    },
    actions:{
        addCanteenObject({commit},data){               
            return new Promise((resolve,reject)=>{                      
                canteenAdd(data).then(res=>{                  
                    if(res.data.status==201){
                        reject("");                       
                    }
                   resolve("");
                },err=>{
                    reject("");
                })
            })
        },
        selectOneCanteen({commit},data){        
            return new Promise((resolve,reject)=>{
                getOneCanteen(data).then(res=>{
                    if(res.data.status==201){
                        reject("");                       
                    }
                    let result=res.data.data
                    commit('setcanteen',result)
                    resolve(result);
                },err=>{
                    reject(""); 
                })
            })
        },
        selectCanteens({commit},data){          
            return new Promise((resolve,reject)=>{
                getCanteenObj(data).then(res=>{                   
                    if(res.data.status==201){
                        reject("");
                    }
                    resolve(res.data.data);
                },err=>{
                    reject("");
                })
            });
        },
        deleteCanteenObj({commit},data){
            return new Promise((resolve,reject)=>{
                deleteCanteen(data).then(res=>{
                    if(res.data.status==201){
                        reject("");
                    }
                    resolve("");
                },err=>{
                    resolve("");
                })
            });
        }
    },

}
export default canteen;